
import React from 'react';
import { render, waitFor, fireEvent } from '@testing-library/react-native';
import RevisionOrchestrator from '../../../src/screens/VocabularyRevision/RevisionOrchestrator';
import * as RevisionSettingsHook from '../../../src/hooks/useRevisionSettings';
import AsyncStorage from '@react-native-async-storage/async-storage';
import { router } from 'expo-router';

// Mocking dependencies
jest.mock('../../../src/hooks/useRevisionSettings');
jest.mock('expo-router', () => ({
  router: {
    push: jest.fn(),
  },
}));
jest.mock('@react-native-async-storage/async-storage', () => ({
  getItem: jest.fn(),
}));

// Mock pour le composant Popup, car nous ne testons pas son implÃ©mentation ici
jest.mock('../../../src/screens/Dashboard/components/popup/RevisionPopup', () => {
  const { View, Text, Button } = require('react-native');
  return ({ visible, onChoice, onDismiss, totalWordsLearned, questionsCount }) => {
    if (!visible) return null;
    return (
      <View>
        <Text>RevisionPopup</Text>
        <Text>Words: {totalWordsLearned}</Text>
        <Text>Questions: {questionsCount}</Text>
        <Button title="Now" onPress={() => onChoice('now')} />
        <Button title="Later 50" onPress={() => onChoice('later_50')} />
        <Button title="Disable" onPress={() => onChoice('disable')} />
        <Button title="Dismiss" onPress={onDismiss} />
      </View>
    );
  };
});

describe('RevisionOrchestrator', () => {
  let mockUseRevisionSettings;
  
  beforeEach(() => {
    // Reset mocks before each test
    jest.clearAllMocks();
    jest.useFakeTimers();

    // Default mock implementation for the hook
    mockUseRevisionSettings = {
      preferences: {
        isDisabled: false,
        nextRevisionAt: 100,
        questionsCount: 10,
        frequency: 50,
      },
      disableRevisions: jest.fn().mockResolvedValue(true),
      resetToNextTarget: jest.fn().mockResolvedValue(true),
      updatePreferences: jest.fn().mockResolvedValue(true),
    };
    RevisionSettingsHook.useRevisionSettings.mockReturnValue(mockUseRevisionSettings);

    // Default mock for AsyncStorage
    AsyncStorage.getItem.mockResolvedValue(JSON.stringify({
      completedWords: { unit1: ['a', 'b', 'c'] } // 3 words
    }));
  });

  afterEach(() => {
    jest.useRealTimers();
  });

  it('renders nothing if revisions are disabled', async () => {
    mockUseRevisionSettings.preferences.isDisabled = true;
    RevisionSettingsHook.useRevisionSettings.mockReturnValue(mockUseRevisionSettings);

    const { queryByText } = render(<RevisionOrchestrator />);
    
    // Wait for any potential state updates
    await waitFor(() => expect(AsyncStorage.getItem).toHaveBeenCalled());

    expect(queryByText('RevisionPopup')).toBeNull();
  });

  it('does not show popup if word count is below threshold', async () => {
    // Total words is 3 (from mock), threshold is 100
    const { queryByText } = render(<RevisionOrchestrator />);
    
    await waitFor(() => expect(AsyncStorage.getItem).toHaveBeenCalled());
    
    // Advance timers to trigger potential popup
    jest.advanceTimersByTime(1500);

    expect(queryByText('RevisionPopup')).toBeNull();
  });

  it('shows the popup when word count reaches the threshold', async () => {
    // Setup word count to be above threshold
    const words = Array.from({ length: 120 }, (_, i) => `word${i}`);
    AsyncStorage.getItem.mockResolvedValue(JSON.stringify({
      completedWords: { unit1: words }
    }));

    const { findByText } = render(<RevisionOrchestrator />);
    
    // Advance timers to trigger the popup's appearance
    jest.advanceTimersByTime(1500);

    const popup = await findByText('RevisionPopup');
    expect(popup).toBeTruthy();
    expect(await findByText('Words: 120')).toBeTruthy();
    expect(await findByText('Questions: 10')).toBeTruthy();
  });

  it('calls resetToNextTarget and navigates when "now" is chosen', async () => {
    // Setup to show popup
    const words = Array.from({ length: 120 }, (_, i) => `word${i}`);
    AsyncStorage.getItem.mockResolvedValue(JSON.stringify({
      completedWords: { unit1: words }
    }));

    const { findByText } = render(<RevisionOrchestrator currentLevel="A1" />);
    jest.advanceTimersByTime(1500);

    const nowButton = await findByText('Now');
    fireEvent.press(nowButton);

    await waitFor(() => {
      expect(mockUseRevisionSettings.resetToNextTarget).toHaveBeenCalledWith(120);
      expect(router.push).toHaveBeenCalledWith({
        pathname: "/tabs/vocabularyRevision",
        params: {
          level: "A1",
          questionsCount: 10,
          source: 'popup_trigger'
        }
      });
    });
  });

  it('calls updatePreferences when "later_50" is chosen', async () => {
    // Setup to show popup
    const words = Array.from({ length: 120 }, (_, i) => `word${i}`);
    AsyncStorage.getItem.mockResolvedValue(JSON.stringify({
      completedWords: { unit1: words }
    }));

    const { findByText } = render(<RevisionOrchestrator />);
    jest.advanceTimersByTime(1500);

    const laterButton = await findByText('Later 50');
    fireEvent.press(laterButton);

    await waitFor(() => {
      expect(mockUseRevisionSettings.updatePreferences).toHaveBeenCalledWith({ nextRevisionAt: 170 });
    });
  });

  it('calls disableRevisions when "disable" is chosen', async () => {
    // Setup to show popup
    const words = Array.from({ length: 120 }, (_, i) => `word${i}`);
    AsyncStorage.getItem.mockResolvedValue(JSON.stringify({
      completedWords: { unit1: words }
    }));

    const { findByText } = render(<RevisionOrchestrator />);
    jest.advanceTimersByTime(1500);

    const disableButton = await findByText('Disable');
    fireEvent.press(disableButton);

    await waitFor(() => {
      expect(mockUseRevisionSettings.disableRevisions).toHaveBeenCalled();
    });
  });

  it('handles dismiss by defaulting to "later_50"', async () => {
    // Setup to show popup
    const words = Array.from({ length: 120 }, (_, i) => `word${i}`);
    AsyncStorage.getItem.mockResolvedValue(JSON.stringify({
      completedWords: { unit1: words }
    }));

    const { findByText } = render(<RevisionOrchestrator />);
    jest.advanceTimersByTime(1500);

    const dismissButton = await findByText('Dismiss');
    fireEvent.press(dismissButton);

    await waitFor(() => {
      expect(mockUseRevisionSettings.updatePreferences).toHaveBeenCalledWith({ nextRevisionAt: 170 });
    });
  });
});
